"use client";

import React from "react";
import {
  Wallet,
  PiggyBank,
  ShieldCheck,
  Sandwich,
  Trophy,
  Gift,
} from "lucide-react";

const benefits = [
  {
    title: "Employees Allowance",
    description: "This is a sample text. Insert your desired text here.",
    icon: Wallet,
    position: "top-0 left-1/2 -translate-x-1/2 -translate-y-full",
    line: "h-20 w-0.5 bg-white absolute top-full left-1/2 -translate-x-1/2",
  },
  {
    title: "Pay Raise",
    description: "This is a sample text. Insert your desired text here.",
    icon: PiggyBank,
    position: "top-12 right-0",
    line: "w-20 h-0.5 bg-white absolute top-6 right-full",
  },
  {
    title: "Achievement Award",
    description: "This is a sample text. Insert your desired text here.",
    icon: Trophy,
    position: "top-1/2 right-0 -translate-y-1/2",
    line: "w-20 h-0.5 bg-white absolute top-1/2 right-full -translate-y-1/2",
  },
  {
    title: "Perks & Bonuses",
    description: "This is a sample text. Insert your desired text here.",
    icon: Gift,
    position: "bottom-12 right-0",
    line: "w-20 h-0.5 bg-white absolute bottom-6 right-full",
  },
  {
    title: "Meal Breaks",
    description: "This is a sample text. Insert your desired text here.",
    icon: Sandwich,
    position: "bottom-0 left-1/2 -translate-x-1/2 translate-y-full",
    line: "h-20 w-0.5 bg-white absolute bottom-full left-1/2 -translate-x-1/2",
  },
  {
    title: "Social Security",
    description: "This is a sample text. Insert your desired text here.",
    icon: ShieldCheck,
    position: "bottom-12 left-0",
    line: "w-20 h-0.5 bg-white absolute bottom-6 left-full",
  },
];

export default function CareerBenefits() {
  return (
    <div className="min-h-screen bg-[#0B2C50] flex flex-col items-center justify-center px-4 py-16">
      <h2 className="text-white text-2xl md:text-3xl font-semibold mb-12 text-center">
        Employee Benefits Diagram Template for PowerPoint
      </h2>

      <div className="relative w-full max-w-5xl h-[550px] flex items-center justify-center">
        {/* Center Icon */}
        <div className="relative z-10 bg-white p-6 rounded-full shadow-xl">
          <PiggyBank className="w-10 h-10 text-green-600" />
        </div>

        {/* Surrounding Benefits */}
        {benefits.map((item, index) => {
          const Icon = item.icon;
          return (
            <div
              key={index}
              className={`absolute ${item.position} flex items-center gap-3 text-white z-0`}
            >
              {/* Connecting Line */}
              <div className={item.line}></div>

              {/* Icon and Text */}
              <div className="flex items-center gap-3 bg-transparent">
                <div className="bg-white p-3 rounded-full">
                  <Icon className="w-6 h-6 text-black" />
                </div>
                <div className="max-w-[150px]">
                  <h4 className="font-semibold text-sm">{item.title}</h4>
                  <p className="text-xs leading-snug">{item.description}</p>
                </div>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
}
